// import store from "@/store"
import axios from '@axios'

export default {
  namespaced: true,
  state: {
    actividad_id: 0,
    tabla: [],
    participantes: [], // participante_id, DNI, PARTICIPANTE, EMAIL, PUEBLO_INDIGENA
    evidencias: [], // id, nombre, archivo
    actividadesDeportivas: [], // id,  nombre, fecha, descripcion
    guardarEditar: true,
  },
  getters: {},
  mutations: {
    listarParticipantes(state, data) {
      // state.participantes = data
      const datax = []
      data.forEach(val => {
        datax.push(
          {
            id: val.id,
            participante: val.persona,
            dni: val.persona.numero_documento,
            participante_id: val.participante_id,
            pueblo_indigena: val.persona.pueblo_indigena.nombre,
          },
        )
      })
      state.participantes = datax
      console.log('_______________-listar participantes: ', state.participantes)
    },
    listarEvidencias(state, data) {
      state.evidencias = data
      console.log('_______________-listar evidencias: ', state.evidencias)
    },
    listarActividades(state, data) {
      state.actividadesDeportivas = data
      console.log('_______________-listar atividades: ', state.actividadesDeportivas)
    },
  },
  actions: {
    fetchActividades(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-cultural/actividad')
          .then(response => { if (response.data.data !== undefined) { ctx.commit('listarActividades', response.data.data) } console.log('actividades GET: ', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    getActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/actividad/${id}`)
          .then(response => { console.log('exitoso', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addActividad(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post('/servicio-cultural/actividad', data)
          .then(response => { console.log('exitoso:', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateActividad(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .put(`/servicio-cultural/actividad/${data.id}`, data)
          .then(response => { console.log('exitoso', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-cultural/actividad/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    searchParticipante(ctx, cadenaBuscar) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/buscar-persona2?query=${cadenaBuscar}`, { withCredentials: false })
          .then(response => { console.log('responseSearchParticipante: ', response); resolve(response) })
          .catch(error => reject(error))
      })
    },

    /* PARTICIPANTES */
    fetchParticipantes(ctx, id) {
      console.log('idque llega a fetchParticipantes: ', id)
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/actividad/${id}/participante`)
          .then(response => { ctx.commit('listarParticipantes', response.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addParticipante(ctx, data) {
      console.log('valores data= ', data)
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/actividad/${data.actividad_id}/participante`, data)
          .then(response => { console.log('exitoso:', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteParticipante(ctx, id) {
      console.log('registro a eliminar:', id)
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-cultural/actividad/participante/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    /* EVIDENCIAS */
    fetchEvidencias(ctx, id) {
      // console.log('idque llega a fetchParticipantes: ', id)
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/actividad/${id}/evidencia`)
          .then(response => { console.log('respuestaArchivos: ', response); ctx.commit('listarEvidencias', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addEvidencia(ctx, data) {
      console.log('ctx :', ctx)
      console.log('valores evidencia= ', data)
      const config = { headers: { 'Content-Type': 'multipart/formdata' } }
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/actividad/${data.get('id')}/evidencia`, data, config)
          .then(response => { console.log('exitoso evidencia:', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteEvidencia(ctx, id) {
      console.log('registro a eliminar:', id)
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-cultural/actividad/evidencia/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
